.card {
  position: relative;

  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 20px 20px 25px;

  border-bottom: 1px solid #ededed;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-template-columns: none;
    grid-template-rows: none;
    padding: 161px 39px 39px;

    background-color: #f2f2f2;
  }

  @media (min-width: $desktop-width) {
    justify-content: flex-end;
  }
}

.card__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0;
  margin-bottom: 22px;

  font-family: $additional-font;
  font-size: 12px;
  line-height: 16px;
  color: #444444;

  @media (min-width: $tablet-width) {
    width: 100%;
    margin-bottom: 33px;

    font-size: 16px;
  }
}

.card__item-title {
  @media (min-width: $tablet-width) {
    padding: 7px 0 9px;

    border-bottom: 1px solid #cdcdcd;
  }
}

.card__item-definition {
  margin: 0;

  text-align: right;

  @media (min-width: $tablet-width) {
    padding: 7px 0 9px;

    border-bottom: 1px solid #cdcdcd;
  }
}

.card__link {
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #222222;
}

.card__title {
  width: 80px;
  margin: 0;
  margin-bottom: 14px;

  font-size: 16px;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    width: 140px;

    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }

  @media (min-width: $desktop-width) {
    line-height: 21px;
  }
}

.card__picture {
  position: relative;

  grid-row: 1/3;
}

.card__img {
  position: absolute;

  grid-row: 1/3;
  align-self: center;
  margin: 0 auto;
}

.card__img--small {
  left: calc(50% - 36px);
  bottom: 17px;

  @media (min-width: $tablet-width) {
    bottom: auto;
    top: -206px;
    left: calc(50% - 81.5px);

    width: 163px;
    height: 172px;
  }
}

.card__img--big {
  left: calc(50% - 42px);
  bottom: 13px;

  @media (min-width: $tablet-width) {
    bottom: auto;
    top: -234px;
    left: calc(50% - 84px);

    width: 168px;
    height: 200px;
  }

  @media (min-width: $desktop-width) {
    top: -240px;

    width: 172px;
  }
}

.card__button {
  display: block;
  grid-column: 1/3;
  align-self: center;
  padding: 10px 0;

  font-size: 16px;
  line-height: 20px;

  @media (min-width: $tablet-width) {
    padding: 10px 93px;
  }

  @media (min-width: $desktop-width) {
    padding: 10px 53px;
  }
}
